html, body {
  height: 100%;
  margin: 0 auto; }

a, a:link, a:visited, a span {
  color: #444;
  text-decoration: none; }

.bb {
  border-bottom: 1px solid #eee;
  padding: 10px;
  margin-bottom: 10px; }

.pull-right {
  float: right; }

.pull-left {
  float: left; }

.sub-title {
  color: gray; }

.center {
  text-align: center; }

.white-icon {
  color: white !important; }

.main .wrapper {
  height: 100%;
  background: #f5f5f5;
  overflow-y: scroll; }

.main .wrapper .content {
  overflow: hidden; }

.assertive {
  color: #ff3c00; }

.card {
  border-radius: 0px;
  box-shadow: none;
  margin: 10px 0px; }

.login .wrapper {
  background: #50bfd3;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.login .wrapper h3.title {
  color: #fff;
  font-weight: 100; }

.login .form-container {
  height: 150px;
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 5px;
  margin-bottom: 20px; }

.login .form-container .row {
  text-align: center;
  margin: 10px auto;
  width: 90%; }

.login .form-container .row input,
.login .form-container .row label {
  line-height: 30px; }

.login .form-container .row input {
  width: 75%;
  margin-left: 2%;
  border: none;
  padding: 5px 10px; }

.login .form-container .row label {
  width: 10%;
  color: #fff; }

.login .login-btn {
  padding: 10px 30px;
  font-size: 16px;
  font-weight: 300;
  background: #fff;
  border: none; }

.bar.bar-calm {
  border-color: #0a9dc7;
  background-color: #49b2c9; }

.notice.list .item {
  margin: 10px 0px;
  padding: 0px 20px; }

.notice.list .item .n-title {
  padding: 10px 0px;
  border-bottom: 1px solid #ddd; }

.notice.list .item .n-title span {
  float: right;
  font-weight: 100; }

.notice.list .item .n-content {
  padding: 10px 0px;
  color: #727272;
  white-space: normal; }

.o-content {
  line-height: 30px; }

.o-title .sub-title {
  margin-left: 20px; }

.item .item-avatar.item {
  border: none; }

.item-avatar {
  line-height: 65px;
  padding-left: 100px; }

.item-avatar > img:first-child {
  border: 1px solid #eee;
  min-width: 65px;
  min-height: 65px;
  border-radius: 0px !important; }
